<template>
  <div class="projectApply-page">
    <!-- 项目立项-项目任务书界面 -->
    <!-- 表格 -->
    <p class="applytitle" style="marginTop:20px">任务书签订/预算书签订</p>
    <!-- 表格 -->
    <el-table
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%"
      @row-click="handleRowClick"
      :header-cell-style="{background:'#EBEBEB'}"
      class="drafts"
    >
      <!-- <el-table-column type="selection" width="55"></el-table-column> -->
      <!-- show-overflow-tooltip控制超过单元格的内容是否用...隐藏 -->
      <el-table-column prop="applyNum" label="申报编号" show-overflow-tooltip width></el-table-column>
      <el-table-column prop="projectName" label="项目名称" show-overflow-tooltip width></el-table-column>
      <el-table-column prop="projectCategory" label="项目类型" show-overflow-tooltip width></el-table-column>
      <el-table-column prop="dependDeptName" label="依托单位" show-overflow-tooltip width></el-table-column>
      <el-table-column prop="projectActionDateStart" label="项目开始时间" show-overflow-tooltip></el-table-column>
      <el-table-column prop="projectActionDateEnd" label="项目结束时间" show-overflow-tooltip width></el-table-column>
      <el-table-column prop="id" label="任务书编号" show-overflow-tooltip width>
        <template slot-scope="scope">
              <span class="row-num" @click.stop="goApplyDetail(scope.row)">{{scope.row.id}}</span>
          </template>
      </el-table-column>
      <el-table-column prop="budgeNum" class="blue" show-overflow-tooltip label="预算书编号" width>
          <template slot-scope="scope">
              <span class="row-num" @click.stop="goBudgetDetail(scope.row)">{{scope.row.id}}</span>
          </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <!-- 按钮 -->
          <span class="dt-txt-btn" @click="handleApply(scope.row)">提交</span>
          <!-- <span class="dt-txt-btn" style="marginLeft:10px" @click="handleApply(scope.row)">提交</span>
          <span class="dt-txt-btn" style="marginLeft:10px" @click="handleApply(scope.row)">立项</span>-->
        </template>
      </el-table-column>
    </el-table>

   <!--  <el-button class="dt-btn" @click="handle1()">院基科费</el-button>
    <el-button class="dt-btn" @click="handle2()" style="marginLeft:10px">校基科费</el-button>
    <el-button class="dt-btn" @click="handle3()" style="marginLeft:10px">重大协同创新</el-button>
    <el-button class="dt-btn" @click="handle4()" style="marginLeft:10px">先导专项</el-button> -->
  </div>
</template>
<script>
/*
 *项目立项-任务书签订 界面
 * @author renchao 2019-02-23
 */
export default {
  data() {
    return {
      centerDialogVisible: false,
      // 表格
      tableData: []
    };
  },
  created(){
    this.getTableData()
  },
  methods: {
    getTableData () {
      this.$dt.getAjax('/project/task',{},(res)=>{
        this.tableData = res.records
      })
    },
    //  跳转对应申请界面
    handleApply(row) {
      //   this.$router.push({
      //     name: row.routeName
      //   });
      // console.log(row);
    },

    handle3() {
      this.$router.push("/inno-collaboration-book");
    },
    handle2() {
      this.$router.push("/assiSchoolExp");
    },
    handle1() {
      this.$router.push("/assiAcademyExp");
    },
    handle4() {
      this.$router.push({
        name:'innoForerunnerBook'
      })
    },
    // 前往项目申请
    goApplyDetail (row) {
      // let routeName = ''
      // switch(row.proType){

      // }

      this.$router.push({
        name: 'innovateForerunner',
        query:{
           id: row.id
        }
      })
    },
    // 前往预算申请
    goBudgetDetail (row) {
       this.$router.push({
        name:'innovateBudget',
        query:{
          id: row.id  
        }
      })
    },
    // 点击每行 前往对应任务书
    handleRowClick (row) {
      this.$router.push({
        name: 'innoForerunnerBook',
        query:{
          id: row.id
        } 
      })
    }
  }
};
</script>
<style style lang="less">
.projectApply-page {
  .applytitle {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 20px;
  }
  @media screen and(max-width: 1600px) {
    .el-table {
      font-size: 14px !important;
    }
    .drafts {
      .dt-txt-btn {
        font-size: 12px;
        margin-left: 0 !important;
      }
    }
  }

  .drafts .el-table__row td:nth-child(n + 7) {
    color: #63b4ff;
  }
  //   表头

  //   表格
  .el-table {
    font-size: 16px;
    border: 1px solid #ebebeb;
  }
  .row-num{
    display: inline-block;
    width: 100%;
    cursor: pointer;
    // text-align: center;
  }
}
</style>





